
import { useContext} from "react";
import {useHistory } from "react-router-dom";
// import {books} from "./Addbook"
import Star from './star-component'
import BookContext from './BookContext'
import {serviceManager} from './services'



function Details(props:any){
    
    const initialState=useContext(BookContext)
    // let selected=state.selectedBook
    const history = useHistory();

    const services = new serviceManager()

            


        return(
            <div>
                        <div className="card">
                        <img style={{width:"200px"}} id="imgDetails" src={initialState.books.selectedBook.cover} alt={initialState.books.selectedBook.title}/>
                        <p><strong>Author:{initialState.books.selectedBook.author}</strong></p> 
                        {/* <p><strong>Rating:{book.rating}</strong></p> */}
                        <Star value={initialState.books.selectedBook.rating}></Star>
                        <p><strong>Price :₹{initialState.books.selectedBook.price}</strong></p>
                        <h2>{initialState.books.selectedBook.title}</h2>
                        <p><strong>{initialState.books.selectedBook.description}</strong></p>
                        {/* {props.valid==="success"?<button id='del-button' onClick={handleClick}>Delete</button>:null} */}
                        {localStorage.getItem("token")?<button id='del-button' onClick={()=>{services.bookDelete(initialState.dispatch,initialState.books.selectedBook._id);history.push("/books");}}>Delete</button>:null}
                        </div>
            </div>
        )
}
export default Details;